@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.login {
    width: 400px;
    height:340px;
    margin: auto;
    background: #f4f4f4;
    padding : 20px;
    color: #000;
    position: relative;
    top:120px;
    border-radius: 30px;
    border:solid 1px black;
    box-shadow: 10px black;
    opacity: 100;
    background-image: url(/SW-Project/static/media/winning-logo.750b343c.jpg);
    box-shadow: 50px;

  }
  .signup{
    position: relative; 
    
    top:15px;
    left:-20px;
    width:390px;
    height:290px; 
    overflow: auto;
   

  }
  .input{
    margin : 10px;
    position: relative;
    left:10px;
    top:30px;
    width: 200px;
    /* height:140px; */
 
   
    text-align: center;
  }
  .title{
    color: rgba(12, 12, 184, 0.918);
    text-align: center;
    position: relative;
    top:15px;
    background-color: rgb(255, 228, 196);
    
  }
.button{
    position: relative;
    margin:20px;
    padding:10px;
    top:80px;
    /* left:40px; */
   
  } 

  
  .submit{
    position: relative; 
    top:30px;
    left:10px;
    background-color: rgb(75, 75, 165);
    color:white;
    border:0px;
    width:200px;
    height:33px; 
    /* border-radius: 10px; */
  }

  .button:hover{
    background-color: red;
    color:white;
    border-radius: 10px;
  }
  .submit:hover{
    background-color: rgb(29, 94, 29);
    color:white;
  }

 
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

